body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#nav{width:100%;-webkit-filter:drop-shadow(0px 1px 3px #000);filter:drop-shadow(0px 1px 3px #000)}.nav-link{padding:0px 25px 0px 25px;border-radius:5px;color:white;font-size:22px;transition:0.3s}.nav-links{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-around;justify-content:space-around;width:30%;margin:auto}.nav-link:hover{background-color:rgba(0,0,0,0.1)}

footer{width:100%;background-color:white;height:40px;margin:0px;bottom:0;padding-top:10px}.footer-links{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-around;justify-content:space-around;margin:auto;width:30vw}.footer-links li:hover{-webkit-filter:drop-shadow(0px 1px 3px #000);filter:drop-shadow(0px 1px 3px #000)}

#info-wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:center;max-width:70vw;margin:100px auto 0 auto}.contact-info{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-around;justify-content:space-around;width:20vw;margin:auto;padding-left:0}.contact-info a:hover{-webkit-filter:drop-shadow(0px 1px 3px #000);filter:drop-shadow(0px 1px 3px #000)}#about-info{margin-top:auto;margin-bottom:auto;padding-bottom:100px;color:white;text-shadow:0px 1px 3px #000}#about-info h4{margin-top:0}#about-info h2{margin-bottom:20px}

a{text-decoration:none;color:navy;font-family:'Nunito', sans-serif}h2{font-family:'Nunito', sans-serif;font-size:40px}h4{font-family:'Nunito', sans-serif;font-size:22px}h3{font-family:'Nunito', sans-serif;font-size:20px}p{font-size:18px}.dark-icon{color:navy}html{scroll-behavior:smooth;background-color:#f5f5f5}body{margin:0px}li{list-style:none;margin-bottom:5px}.seperator{border-bottom:2px solid gray}

#skills{width:70vw;margin:auto;margin-bottom:40px}.skills-list{list-style:none;padding-left:0;font-size:20px;font-family:'Montserrat', sans-serif}.wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;margin:auto;list-style:none;text-align:left;padding-left:0}@media screen and (min-width: 992px){#skills{height:40vh}.wrapper{-webkit-flex-flow:row wrap;flex-flow:row wrap}}

.git-link{position:absolute;bottom:0;left:0;margin-bottom:5px;margin-left:5px}.project-square{border:2px solid black;padding:5px;background-color:white;border-radius:5px;position:relative}.project-square li{font-family:'Montserrat', sans-serif}.project-square:hover{box-shadow:0px 10px 13px -7px #000,5px 5px 15px 5px rgba(0,0,0,0)}#projects{width:90vw;margin:auto;margin-bottom:40px}#projects-grid{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.project-image{width:100%;height:40%;object-fit:cover;border:1px solid black;border-radius:5px;margin:auto}.project-list{padding-left:0;padding-bottom:10px}@media screen and (min-width: 576px){#projects-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));grid-gap:10px}}@media screen and (min-width: 992px){#projects{max-width:80vw}}



.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.opener {
  height: 80vh;
  background-color: #bdd4e7;
  background-image: linear-gradient(315deg, #bdd4e7 0%, #8693ab 74%);
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

